






































































































import $, {ready} from "jquery"
import 'slick-carousel';
import 'slick-carousel/slick/slick.css';
import 'animate.css/animate.min.css';
import Map from "@/components/Map.vue";
import Vue from "vue"
import {Component, Prop, Watch} from 'vue-property-decorator';

@Component({
  name: "App",
  components: {
    Map
  }
})

export default class App extends Vue {
  mounted() {
    this.homeSlider()
    $(window).scroll(() => {
      if (pageYOffset === 0) {
        $(".header").removeClass("scroll")
      } else {
        $(".header").addClass("scroll")
      }
    })
  }

  homeSlider() {
    $(".home__sliders").slick({
      lazyLoad: "ondemand",
      arrows: false,
      dots: true,
      fade: true,
      infinite: true,
      autoplay: true,
      autoplaySpeed: 7000,
      speed: 1000,
      slidesToShow: 1,
      slidesToScroll: 1
    })
    $('.home__sliders').on('beforeChange', function (event, slick, currentSlide, nextSlide) {
      let slide = $(".slider");
      slide.eq(nextSlide).children().addClass("animate__animated animate__fadeInUp");
      slide.eq(currentSlide).children().removeClass("animate__animated animate__fadeInUp");
    })
  }

}

